.about__container {
  display: grid;
  grid-template-columns: 35% 55%;
  gap: 10%;
  place-items: center;
}

.about__me {
  width: 100%;
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: scale(1, 1);
  transition: var(--transition);
}

.about__me-image:hover {
  transform: scale(1.05, 1.05);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about__card {
  background-color: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: var(--transition);
}

.about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about__icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about__card h5 {
  font-size: 0.95rem;
}

.about__card small {
  font-size: 0.7rem;
  color: var(--color-light);
}

.about__content p {
  margin: 2rem 0 0;
  color: var(--color-light);
}

.coding_profiles {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 1rem 0 2rem;
}

.coding_profile {
  display: flex;
  justify-items: center;
  align-items: center;
}

.profile_icon {
  margin: 1rem;
  font-size: 2.25rem;
}

/* =========== MEDIA QUERIES (MEDIUM DEVICES) =========== */

@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about__content p {
    margin: 1.5rem 0 2rem;
  }
}

/* =========== MEDIA QUERIES (SMALL DEVICES) =========== */

@media screen and (max-width: 600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr;
    gap: 1rem;
    place-items: center;
  }

  .about__card {
    width: 70%;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 0;
  }
}
